import React, { useEffect } from 'react';
import  Carousel from "react-bootstrap/Carousel";
import "./index.scss"; // 引入自定義的 SCSS 文件
import { AdData } from '../../type/adsType';
import { bannerNavigate } from '../../utils/helper';
import { useNavigate } from 'react-router-dom';
interface CarouselProps {
  data: AdData[] | null;
  imgHost: string;
};
const Carousels: React.FC<CarouselProps> = ({ data, imgHost }) => {
  const navigate = useNavigate();

  const handleImgClick = (link: string) => {
    const result = bannerNavigate(link);
    if (typeof result === "object") {
      console.log("route: ", result?.route);
      console.log("state: ", result?.state);
    }  
  
    if (result !== null && typeof result === 'object') {
      navigate(result?.route, {state: result?.state });
    } else {
      const a = document.createElement('a');
      a.href = result;
      a.target = '_blank'; // 或同分頁就是 '_self'
      a.click();
    }
  }
  return (
    <Carousel indicators={true} controls={false}>
      {Array.isArray(data) && data?.map((item, i) => {
        return (
          <Carousel.Item 
            onClick={() => {}}
            style={{
              cursor: "pointer",
              width: "100%",
              maxHeight: "auto", // 可根據比例調整 
            }}
            key={i}
          >
            {item.link_url.match(/\.(mp4|webm|ogg)$/i) ? (
              <video
                className="d-block w-full h-full"
                src={'https://tdtest84-ag.qcpro.vip/reseller/images/banner/20230223_QC_300100_CN.gif'}
                autoPlay
                muted
                loop
                style={{ objectFit: "cover" }} // 讓影片適應容器
                onError={(e: React.SyntheticEvent<HTMLVideoElement, Event>) => {
                  const target = e.currentTarget;
                  target.onerror = null; // Prevents infinite loop in case fallback also fails
                  target.src = "https://player.vimeo.com/external/374265480.hd.mp4?s=2753a67069f7a1b9e191772da00c55042b805a3b&profile_id=175"; // Fallback video URL
                }}
              >
                Your browser does not support the video tag.
              </video>
            ) : (
              <div onClick={() => handleImgClick(item?.link_url)}>
                <img
                  className="d-block w-full h-full object-contain"
                  src={`${imgHost}${item?.img_url}`}
                  alt={item?.b_description || "Image"}
                  onError={(e) => {
                    e.currentTarget.onerror = null;
                    e.currentTarget.src = '';
                  }}
                />
              </div>
            )}
          </Carousel.Item>
        );
      })}
    </Carousel>
  );
}
export default Carousels;
