import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { AppDispatch, RootState } from "../../store";
import { useDispatch, useSelector } from "react-redux";
import { fetchAds } from "../../store/adsSlice";
import { bannerNavigate, getImage } from "../../utils/helper";
import { useNavigate } from "react-router-dom";
interface AdBannerProps {
  styleProps?: React.CSSProperties;
}
const AdBanner: React.FC<AdBannerProps> = ({ styleProps }) => {
  const [isVisible, setIsVisible] = useState<boolean>(true);
  const dispatch:AppDispatch = useDispatch();
  const navigate = useNavigate();
  const adData = useSelector((state: RootState) => state.ads.adData);
  const init = useSelector((state: RootState) => state.init.init);
  const handleClose = (): void => {
    setIsVisible(false);
  };

  const handleImgClick = (link: string) => {
    const result = bannerNavigate(link);
    if (result !== null && typeof result === 'object') {
      navigate(result?.route, {state: result?.state });
    } else {
      const a = document.createElement('a');
      a.href = result;
      a.target = '_blank'; // 或同分頁就是 '_self'
      a.click();
    }
  }

  useEffect(() => {
    dispatch(fetchAds(64));
  }, [dispatch]);

  useEffect(() => {
    console.log("adData: ", adData);
    console.log("init: ", init);
  }, [adData, init]);

  if (!isVisible) return null;

  return (
    <>
      {adData['64'] && Array.isArray(adData['64'].data) && (
        <div
          className="fixed bottom-0 left-0 right-0 bg-gray-100 shadow-lg mx-auto"
          style={{ maxWidth: "100%", width: "100%", height: "12%", ...styleProps}}
          onClick={() => handleImgClick(adData['64']?.data[0]?.link_url)}
        >
          <div className="relative flex justify-center items-center max-w-full h-full">
            {/* Ad Image 728x90 */}
            <img
              className="d-block w-full h-full object-contain"
              src={`${init?.img_host}${adData['64']?.data[0]?.img_url}`}
              alt={"Image"}
              onError={(e) => {
                e.currentTarget.onerror = null;
                e.currentTarget.src = '';
              }}
            />
            {/* Close Button */}
            <button
              onClick={(e) => {
                e.stopPropagation();
                handleClose();
              }}
              className="absolute w-8 h-8 top-2 right-2 bg-transparent text-gray-700 hover:text-gray-900"
            >
              <FontAwesomeIcon icon={faTimes} size="lg" />
            </button>
          </div>
        </div>
      )}
    </>
  );
};

export default AdBanner;
