import { useState, useContext, useEffect } from 'react';
import { faChevronDown, faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { VariableContext } from "../../pages/Section"; 
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatch, RootState } from '../../store';
//import { fetchTags, selectTags } from '../../store/tagsSlice';
import { Button, FormControlLabel, Popover, List, ListItem, ListItemText  } from '@mui/material';
import { Checkbox } from '@mui/material';
import { fetchVideoList } from '../../store/videoSlice';
import { VideoListData, VideoListDetail } from '../../type/videoType';
import { mode } from 'crypto-js';
import { useTranslation } from 'react-i18next';
import { getFontClass } from '../../utils/helper';
import { Language } from '@mui/icons-material';

 
interface TagSelectorProp {
  config: {
    title: string;
    cate: string;
    index: number;
    selector?: boolean;
    count?: number;
  };
}
const TagSelector: React.FC<TagSelectorProp> = ({config}) => {
  const {t} = useTranslation();
  const { setTagSelectorVar } = useContext(VariableContext);
  const video = useSelector((state: RootState) => state.video.videoList);
  const language = useSelector((state: RootState) => state.init.language);
  const dispatch:AppDispatch = useDispatch();

  const [tags, setTags] = useState<any[]>([]);
  const [selectedTag, setSelectedTag] = useState<number>(0);

  useEffect(() => {
    if (config.selector && video && config.cate in video) {
      const data = video[config.cate] as VideoListDetail;
      setTags(data.subCategory.map((item, index) => index === 0 ? t("全部") : item[`tag_name_${language}`]));
    }
  }, [video]);
  

  useEffect(() => {
    if (selectedTag) {
      setTagSelectorVar(selectedTag);
    }
  }, [selectedTag]);

  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleSelectedTag = (index: number) => {
    setSelectedTag(index);

    dispatch(fetchVideoList({ 
      name: config.cate,
      url: `?cate=${config.index}&cate_c=${index}`,
      type: 'category'
    }));
  }

  const open = Boolean(anchorEl);
  const id = open ? 'settings-popover' : undefined;

  return (
    <div className="flex justify-between w-full h-20">
      {/* 左侧：标题和过滤器 */}
      <div className="flex justify-between items-end w-full relative">
        <div 
          className="flex flex-col  items-start space-x-4 w-full relative"
          style={{paddingLeft: '10px'}}
        >
        {config.selector && (
          <>
            <Button
              aria-describedby={id}
              variant="contained"
              color="primary"
              onClick={handleClick}
              className="!bg-gray-800 hover:!text-gray-400"
              disableElevation
            >
              {t("篩選器")}
              <FontAwesomeIcon
                icon={faChevronDown}
                className={`ml-2 text-primary-active transform transition-transform duration-300`}
              />
            </Button>

            <Popover
              id={id}
              open={open}
              anchorEl={anchorEl}
              onClose={handleClose}
              disablePortal
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'center',
              }}
              transformOrigin={{
                vertical: 'top',
                horizontal: 'center',
              }}
              sx={{
                "& .MuiPaper-root": {
                  backgroundColor: "transparent", // 設置為完全透明
                },
              }}
            >
              <div className="min-w-3/5 mt-1 bg-gray-800 text-gray-200 flex flex-wrap rounded-lg pl-2 pt-2 pb-2 ">
                {tags?.map((tag, index) => (
                  <span
                    key={index}
                    className={
                      `flex items-center justify-center rounded-full text-sm px-2 py-1 m-1 bg-gray-900 }
                      ${selectedTag === index ? 'bg-red-500' : 'bg-black'}`
                    }
                    onClick={() => handleSelectedTag(index)}
                  >
                    {tag}
                  </span>
                ))}
              </div>
            </Popover>
          </>
        )} 
        </div> 
        <h2 className={getFontClass(language, ["font-bold", "w-full", "text-right"])}>{t(`共{{number}}部`, {number: config.count})}</h2>
      </div>
    </div>
  );
};
export default TagSelector;
