import React, { useState, useEffect, createContext, useContext } from "react";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";

import { useDispatch, useSelector } from "react-redux";
import { RootState, AppDispatch } from "../../store";
import { fetchAds } from "../../store/adsSlice";
import { fetchVideoList, search } from "../../store/videoSlice";

import VideoMap from "../../components/Video/VideoMap";
import AdBanner from "../../components/AdBanner";
import AppBar from "../../components/AppBar";
import Carousels from "../../components/Carousels";
import VideoToolbar from "../../components/Video/VideoToolbar";
import TagSelector from "../../components/TagSelector";
import ComicMap from "../../components/Comic/ComicMap";

import * as comic from "../../type/comicType";
import * as video from "../../type/videoType";
import { CategoryData } from "../../type/categoryType";
import Search from "../Search";
import { Product } from "../../type/videoType";
import Loading from "../../components/Loading";

export const VariableContext = createContext<any>(null);

const Section: React.FC = () => {
  const { t } = useTranslation();
  const location = useLocation();
  const state = location.state as {
    cate: string;
    title: string;
    index: number;
    searchUrl: string;
    mode: string;
  };

  const [allIndexLayout, setAllIndexLayout] = useState<Product[]>([]);
  const [allVideo, setAllVideo] = useState<Product[]>([]);
  const [allActorSearch, setAllActorSearch] = useState<Product[]>([]);
  const [tagSelectorVar, setTagSelectorVar] = useState<number>(0);
  const [loading, setLoading] = useState(true);

  const dispatch: AppDispatch = useDispatch();

  const adData = useSelector((state: RootState) => state?.ads?.adData);
  const init = useSelector((state: RootState) => state?.init?.init);
  const video = useSelector((state: RootState) => state?.video?.videoList);
  const indexLayoutList = useSelector(
    (state: RootState) => state?.video?.indexLayoutList
  );
  const indexLayout = useSelector(
    (state: RootState) => state?.video?.indexLayout
  );
  const category = useSelector((state: RootState) => state?.category?.category);
  const actorSearch = useSelector(
    (state: RootState) => state?.video?.searchVideoData
  );

  const handleGetMoreIndexLayout = () => {
    const targetIndexLayout = indexLayoutList?.find(
      (item) => item?.name === state?.cate
    );
    if (indexLayout && targetIndexLayout) {
      switch (targetIndexLayout?.api) {
        case "list":
          dispatch(
            fetchVideoList({
              name: targetIndexLayout?.name,
              url: targetIndexLayout?.search,
              type: "mv",
              mode: "indexLayout",
              num: Number(indexLayout[state?.cate]?.pageNum) + 1
            })
          );
          break;
        default:
          const config: video.SearchParam = {
            url: `${targetIndexLayout?.search?.replace("?", "&")}&num=${Number(indexLayout[state?.cate]?.pageNum) + 1}`,
            name: targetIndexLayout?.name,
            mode: "indexLayout",
          };
          dispatch(search(config));
          break;
      }
    }
  };

  const handleGetMoreVideo = () => {
    if (video && category) {
      const catConfig: CategoryData = category?.find(
        (item) => item?.name === state?.cate
      )!;
      dispatch(
        fetchVideoList({
          name: catConfig?.name,
          url: `${catConfig?.url}&cate_c=${tagSelectorVar}`,
          type: catConfig?.type,
          num: Number(video[state?.cate]?.pageNum) + 1,
        })
      );
    }
  };

  const handleGetMoreActorSearch = () => {
    dispatch(
      search({
        url: state?.searchUrl,
        num: Number(actorSearch?.pageNum) + 1,
      })
    );
  };

  const updateAllActorSearch = () => {
    if (actorSearch && actorSearch?.products && Number(actorSearch?.pageNum) !== 1) {
      setAllActorSearch((prevAllActorSearch) => [
        ...prevAllActorSearch,
        ...actorSearch?.products,
      ]);
    }
  };

  const updateAllVideo = () => {
    if (video && video[state?.cate] && Number(video[state?.cate]?.pageNum) !== 1) {
      setAllVideo((prevAllVideo) => [
        ...prevAllVideo,
        ...video[state?.cate]?.products,
      ]);
    }
  };

  const updateAllIndexLayout = () => {
    if (indexLayout && indexLayout[state?.cate] && Number(indexLayout[state?.cate]?.pageNum) !== 1) {
      setAllIndexLayout((prevAllIndexLayout) => [
        ...prevAllIndexLayout,
        ...indexLayout[state?.cate]?.products,
      ]);
    }
  };

  const handleInitAllActorSearch = () => {
    if (actorSearch && Number(actorSearch?.pageNum) === 1) {
      setAllActorSearch([]);
      setAllActorSearch(actorSearch?.products);
    }
  };

  const handleInitAllVideo = () => {
    if (video && video[state?.cate] && Number(video[state?.cate]?.pageNum) === 1) {
      setAllVideo([]);
      setAllVideo(video[state?.cate]?.products);
    }
  };

  const handleInilAllIndexLayout = () => {
    if (indexLayout && Number(indexLayout[state?.cate]?.pageNum) === 1) {
      setAllIndexLayout([]);
      setAllIndexLayout(indexLayout[state?.cate]?.products);
    }
  };

  const handleInitAllIndexLayout = () => {
    if (indexLayout && indexLayout[state?.cate] && Number(indexLayout[state?.cate]?.pageNum) === 1) {
      setAllIndexLayout([]);
      setAllIndexLayout(indexLayout[state?.cate]?.products);
    }
  };

  useEffect(() => {
    handleInitAllVideo();
    handleInitAllActorSearch();
    handleInilAllIndexLayout();
    updateAllVideo();
    updateAllActorSearch();
    updateAllIndexLayout();
  }, [video, actorSearch, indexLayout]);

  useEffect(() => {
    if (state?.mode !== "indexLayout" && category) {
      const catConfig: CategoryData = category?.find(
        (item) => item?.name === state?.cate
      )!;
      if (catConfig?.type === "search") {
        dispatch(search({ url: state?.searchUrl }));
      } else {
        dispatch(
          fetchVideoList({
            name: catConfig?.name,
            url: `${catConfig?.url}&cate_c=${tagSelectorVar}`,
            type: catConfig?.type,
          })
        );
      }
    } else {
      const targetIndexLayout = indexLayoutList?.find(
        (item) => item?.name === state?.cate
      );
      if (targetIndexLayout) {
        switch (targetIndexLayout?.api) {
          case "list":
            dispatch(
              fetchVideoList({
                name: targetIndexLayout?.name,
                url: targetIndexLayout?.search,
                type: "mv",
                mode: "indexLayout",
              })
            );
            break;
          default:
            const config: video.SearchParam = {
              url: targetIndexLayout?.search?.replace("?", "&"),
              name: targetIndexLayout?.name,
              mode: "indexLayout",
            };
            dispatch(search(config));
            break;
        }
      }
    }
  }, [category, state, dispatch]);

  useEffect(() => {
    dispatch(fetchAds(63)); // 每次進入頁面時檢查是否需要更新廣告
  }, [dispatch]);

  useEffect(() => {
    setTimeout(() => {
      window.scrollTo(0, 1);
      window.scrollTo(0, 0);
    }, 100);
    // 強制觸發瀏覽器 repaint，避免初次畫面為空白
    setTimeout(() => {
      setLoading(false);
    }, 500);
  }, []);

  return (
    <div>
      {loading ? (
        <div>
          <Loading />
        </div>
      ) : (
        <VariableContext.Provider value={{ tagSelectorVar, setTagSelectorVar }}>
          <AppBar type={"section"} />
          <Carousels
            data={adData["63"]?.data ?? []}
            imgHost={init?.img_host ?? ""}
          />
          {state?.cate !== "女優" && (
            <>
              <VideoToolbar
                title={`${state?.title}`}
                filterComponent={
                  state?.mode === "indexLayout" ? (
                    <TagSelector
                      config={{
                        selector: false,
                        title: state?.title,
                        cate: state?.cate,
                        index: state?.index,
                        count: indexLayout?.[state?.cate!]?.productTotal,
                      }}
                    />
                  ) : (
                    <TagSelector
                      config={{
                        selector: true,
                        title: state?.title,
                        index: Number(state?.index)+1,
                        count: video?.[state?.title!]?.productTotal,
                        cate: state?.cate,
                      }}
                    />
                  )
                }
              />
              <VideoMap
                data={
                  state?.mode === "indexLayout"
                    ? allIndexLayout ?? []
                    : allVideo ?? []
                }
                onReachBottom={state?.mode === "indexLayout" ? handleGetMoreIndexLayout : handleGetMoreVideo}
                lastPage={
                  state?.mode === "indexLayout"
                    ? indexLayout && Number(indexLayout[state?.cate]?.pageNum) === Number(indexLayout[state?.cate]?.pageTotals)
                    : video && Number(video[state?.cate]?.pageNum) === Number(video[state?.cate]?.pageTotals)
                }
                cate={state?.cate}
              />
            </>
          )}
          {(state?.cate === "女優" || state?.cate === t("女優")) &&
            actorSearch && (
              <>
                <VideoToolbar
                  title={t(`${state?.title}`)}
                  filterComponent={
                    <TagSelector
                      config={{
                        selector: false,
                        title: state?.title,
                        cate: state?.cate,
                        index: state?.index,
                        count: actorSearch?.productTotal,
                      }}
                    />
                  }
                />
                <VideoMap
                  data={allActorSearch ?? []}
                  onReachBottom={handleGetMoreActorSearch}
                  lastPage={Number(actorSearch?.pageNum) === Number(actorSearch?.pageTotals)}
                />
              </>
            )}
          <AdBanner />
        </VariableContext.Provider>
      )}
    </div>
  );
};
export default Section;
