import { useEffect, useState } from "react";
import ToggleButtoms from '../../ToggleButtons'; 
import { JsxElement } from "typescript";
import LoadMore from "../../LoadMore";

interface VideoToolbarProps {
  title?: string;
  toggleButtons?: { label: string; value: string }[];
  videoCount?: number;
  filterComponent?: JSX.Element|null;
  loadMore?: () => void;
}
  
const VideoToolbar: React.FC<VideoToolbarProps> = ({
  title,
  toggleButtons,
  videoCount,
  filterComponent,
  loadMore,
}) => {

  return (
    <div className="flex px-4 items-end justify-start w-full">
      <h2 className="text-xl font-bold whitespace-nowrap py-1">{title}</h2>
      {toggleButtons && (
        <ToggleButtoms  data={toggleButtons}/>
      )}
      {filterComponent && (
        filterComponent
      )}
       {loadMore && (
        <LoadMore onLoadMore={loadMore}/>
      )}
    </div>
  );
};
export default VideoToolbar;
  