import React, { useState } from 'react';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import ToggleButton from '@mui/material/ToggleButton';

interface ToggleButtonsProps {
    data: {label:string, value:string}[];
}

const ToggleButtons: React.FC<ToggleButtonsProps> = ({ data }) => {
  const [alignment, setAlignment] = useState<string | null>(data[0].value);

  const handleAlignment = (
    event: React.MouseEvent<HTMLElement>,
    newAlignment: string | null
  ) => {
    if (newAlignment !== null) {
      setAlignment(newAlignment);
    }
  };

  return (
    <ToggleButtonGroup
      value={alignment}
      exclusive
      onChange={handleAlignment}
      aria-label="text alignment"
      sx={{
        display: 'flex', // 使用 Flexbox 確保按鈕水平排列
        justifyContent: 'space-between', // 使按鈕之間有適當的間距
        width: '6rem'
      }}
    >
      {data.map((item, index) => (
        <ToggleButton 
          key={index} 
          value={item.value} 
          sx={{
            color: 'white !important',
            padding: '1px',
            width: '2.5rem',
            borderBottom: alignment === item.value ? '2px solid red' : 'none', // 修正這裡的邏輯
          }}
        >
          {item.label}
        </ToggleButton>
      ))}
    </ToggleButtonGroup>
  );
};
export default ToggleButtons;